export const hashById = array => array.reduce((acc, curr) => {
  acc[curr.id] = curr;
  return acc;
}, {});
export const hashArraysInObject = function (result) {
  let hashFunction = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : hashById;
  return Object.keys(result).reduce((acc, currKey) => {
    const prop = result[currKey];

    if (Array.isArray(prop)) {
      acc[currKey] = hashFunction(result[currKey]);
    } else {
      acc[currKey] = prop;
    }

    return acc;
  }, {});
};