import { useQuery } from 'react-query';
export default function useUserOrgUnits() {
  const {
    isLoading: loading,
    error,
    data
  } = useQuery(['me', {
    params: {
      fields: ['organisationUnits[id]']
    }
  }], {
    select: data => data.organisationUnits.map(_ref => {
      let {
        id
      } = _ref;
      return id;
    })
  });
  return {
    data,
    loading,
    error
  };
}